@import "../../../css/module/color";

$rightWidth: 250px;

#leftSec{
	position: absolute;
	top: 0;
	left: 0;
	right: $rightWidth;
	bottom: 0;
	padding: 20px;
	overflow: auto;
	border-right: 1px solid lighten($sidebar-bg , 5%);
}

#rightSec{
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	width: $rightWidth;
	overflow: auto;
	padding: 2em;
}